<template>
  <div class="session">
      <h1>{{ session.title }}</h1>

      <!-- <div class="mb-4">Exercice {{ currentExoIdx+1 }}/{{ session.exercices.length }}</div> -->
      <v-progress-linear :value="progress"></v-progress-linear>

      <component
        v-for="(exercice, exoIdx) in session.exercices" :key="exoIdx"
        v-show="exoIdx==currentExoIdx"
        :is="session.exercices[exoIdx].type"
        :exercice="session.exercices[exoIdx]"
      />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import qcm from '@/components/Exos/qcm.vue'
import question from '@/components/Exos/question.vue'

export default {
  name: 'Session',
  props: {
    id: {
      required: true
    }
  },
  created() {
    this.resetCurrent()
    this.setCurrentSession(this.id)
    this.setCurrentExo(0)
  },
  data() {
    return {
    }
  },
  components: {
    qcm, question
  },
  methods: {
    ...mapMutations([
      'setCurrentSession', 'setCurrentExo', 'resetCurrent'
    ]),
  },
  computed: {
    session () {
      return this.getSession(this.id)
    },
    progress () {
      if (this.currentExoIdx === this.session.exercices.length) {
        (new Audio(require('@/assets/sounds/finish.mp3'))).play();
      }
      return (this.currentExoIdx)*100 / this.session.exercices.length
    },
    ...mapGetters({
      getSession: 'getSession',
      currentExoIdx: 'getCurrentExoIdx'
    }),
  }
}
</script>
